import "purecss/build/pure.css";
import * as React from "react";
import { useEffect, useState } from "react";
import "../styles.scss";

import {
    Box,
    Button,
    Container,
    Link as MuiLink,
    Stack,
    TextField,
    Typography
} from "@mui/material";
import API from "../utils/API";

import { getLocaleText, I18nText } from "../utils/I18n";

const example_training_results = {
    "userId": 1,
    "results": [
        {
            "wordId": 1,
            "correct": true
        },
        {
            "wordId": 2,
            "correct": true
        },
        {
            "wordId": 3,
            "correct": true
        },
        {
            "wordId": 4,
            "correct": false
        },
        {
            "wordId": 5,
            "correct": false
        }
        // ...
    ]
};

interface Document {
    id: string;
    title: string;
    content: string;
}

type JyutpingMapping = { [character: string]: string[] };

const parseRimeDictionary = (content: string): JyutpingMapping => {
    const lines = content.split('\n');
    const mapping: JyutpingMapping = {};

    const lineRegex = /^(.)\s+([a-z0-9]+)(\s+\d+%?)?$/;

    for (const line of lines) {
        const match = line.match(lineRegex);
        if (match) {
            const [, character, jyutping] = match;
            if (!mapping[character]) {
                mapping[character] = [jyutping];
            } else {
                // 
                if (!mapping[character].includes(jyutping)) {
                    mapping[character].push(jyutping);
                }
            }
        }
    }

    return mapping;
};

export default function Home(props: { lang: keyof I18nText }) {
    const { lang } = props;

    const [messageSent, setMessageSent] = useState<boolean>(false);
    const [searchValue, setSearchValue] = useState<string>(''); // 
    const [searchResults, setSearchResults] = useState<Document[]>([]);
    const [characters, setCharacters] = useState<string[]>([]);
    const [jyutpingMapping, setJyutpingMapping] = useState<JyutpingMapping | null>(null);

    // roma 
    const [isExerciseVisible, setIsExerciseVisible] = useState<boolean>(false);
    const [currentCharacter, setCurrentCharacter] = useState<string>('');
    const [userInput, setUserInput] = useState<string>('');
    const [feedback, setFeedback] = useState<string>('');

    const sendMessage = async () => {
        try {
            // 
            await API.post('/api/training/results', example_training_results); //  axios 
            setMessageSent(true); //  true
        } catch (error) {
            console.error(":", error);
        }
    };

    const handleSearch = async () => {
        // 
        console.log(':', searchValue);
        try {
            // 
            const response = await API.get('/api/search', { params: { query: searchValue } }); //  axios 
            console.log(':', response.data); // 
            setSearchResults(response.data);
        } catch (error) {
            console.error(":", error);
        }
    };

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://raw.githubusercontent.com/nk2028/commonly-used-chinese-characters-and-words/main/char.txt');
                const text = await response.text();
                const charArray = text.split('\n').filter(char => char.trim() !== ''); // 
                setCharacters(charArray);
                console.log(" " + charArray.length)
            } catch (error) {
                console.error('Failed to fetch data:', error);
            }
        };

        fetchData();
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://raw.githubusercontent.com/rime/rime-cantonese/main/jyut6ping3.chars.dict.yaml');
                const text = await response.text();
                const parsedMapping = parseRimeDictionary(text);
                setJyutpingMapping(parsedMapping);
            } catch (error) {
                console.error('Failed to fetch data:', error);
            }
        };

        fetchData();
    }, []);

    const startExercise = () => {
        if (characters.length > 0) {
            const randomIndex = Math.floor(Math.random() * characters.length);
            const randomCharacter = characters[randomIndex];
            setCurrentCharacter(randomCharacter);
            setUserInput('');
            setFeedback('');
            setIsExerciseVisible(true);
        }
    };

    const checkAnswer = () => {
        if (currentCharacter && jyutpingMapping) {
            const correctAnswers = jyutpingMapping[currentCharacter];
            if (correctAnswers && correctAnswers.includes(userInput.trim())) {
                setFeedback(' ' + correctAnswers + '');
            } else {
                setFeedback(' ' + correctAnswers + '');
            }
        }
    };

    return (
        <Container maxWidth="md">
            <Typography variant="h5" sx={{ marginBottom: 2 }}>
                {getLocaleText(
                    {
                        "en": " Studling",
                        "zh-Hant": " Studling",
                        "zh-Hans": " Studling",
                        "tto-bro": "EeRZ T8eHXQea",
                        "tto": "hFCmo mAFKRHm",
                        "ja": " Studling",
                        "de": " Studling",
                    },
                    lang
                )}
            </Typography>
            <Typography variant="body1" sx={{ marginBottom: 2 }}>
                {getLocaleText(
                    {
                        "en": " Studling",
                        "zh-Hant": " Studling",
                        "zh-Hans": "",
                        "tto-bro": "EeRZ T8eHXQea",
                        "tto": "hFCmo mAFKRHm",
                        "ja": " Studling",
                        "de": " Studling",
                    },
                    lang
                )}
            </Typography>


            <Box marginBottom={2}>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={startExercise}
                >
                    
                </Button>
            </Box>

            {isExerciseVisible && (
                <Box marginBottom={4}>
                    <Typography variant="h6"></Typography>
                    <Typography variant="h4" sx={{ marginBottom: 2 }}>{currentCharacter}</Typography>
                    <TextField
                        label=""
                        variant="outlined"
                        value={userInput}
                        onChange={(e) => setUserInput(e.target.value)}
                        sx={{ marginBottom: 2 }}
                        autoComplete="off"
                    />
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={checkAnswer}
                        sx={{ margin: 2 }}
                    >
                        
                    </Button>
                    {feedback && <Typography sx={{ marginTop: 2 }}>{feedback}</Typography>}
                    {feedback && <Button
                        variant="contained"
                        color="secondary"
                        onClick={startExercise}
                    >
                        
                    </Button>}
                </Box>
            )}


            <Box marginBottom={4}>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={sendMessage}
                    disabled={messageSent} // 
                >
                    
                </Button>
            </Box>

            <Stack direction="row" spacing={2} alignItems="center" sx={{ marginBottom: 2 }}> {/*  Stack  */}
                <TextField
                    label=""
                    variant="outlined"
                    value={searchValue} // 
                    onChange={(e) => setSearchValue(e.target.value)} // 
                    autoComplete="off"
                />
                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleSearch} //  handleSearch 
                >
                    
                </Button>
            </Stack>

            {/*  */}
            <Box sx={{ marginTop: 2 }}>
                {searchResults.map((doc, index) => (
                    <Box key={doc.id} sx={{ marginBottom: 2, border: '1px solid #ccc', padding: 2 }}>
                        <Typography variant="h6">{doc.title}</Typography>
                        <Typography variant="body1">{doc.content}</Typography>
                    </Box>
                ))}
            </Box>
        </Container>
    );
}
